.gallery {
    &__wrapper-outer {
        position: relative;
        height: 100%;
        width: 100%;
    }
    &__wrapper-outer::before {
        content: '';
        z-index: -1;
        position: absolute;
        background-color: #4A4A4A;
        left: 0;
        top: 0;
        right: 0;
        bottom: 15%;
    }
    &__wrapper-inner {
        padding-top: 100px;
        display: flex;
        flex-direction: row;
    }
}
.slider {
    &-card {
        position: relative;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        margin: 0 auto;
        &__img {
            width: 400px;
            height: auto;
        }
        &__text {
            height: 100%;
            display: flex;
            flex-direction: column;
            margin-left: 100px;
            &-title{
                font-family: 'Block-condensed';
                font-size: 60px;
                color: #FFFFFF;
                line-height: 100px;
                letter-spacing: 16px;
            }
            &-description {
                width: 370px;
                color: #FFFFFF;
                font-family: 'Effra';
                font-size: 20px;
                line-height: 30px;
                font-weight: 300;
            }
        }
    }
}
